//display en index.html

.home-wrapper {
    @include lg {
        display: grid;
        grid-gap: 30px;
        grid-template-columns: auto auto;
        .a {
            grid-column: 2;
            grid-row: 1;
            h2 {
                padding-top: 40px;
            }
        }
        .b {
            grid-column: 1;
            grid-row: 1 / 4;
            h2 {
                padding-top: 40px;
            }
        }
        .c {
            grid-column: 2;
            grid-row: 2;
        }
        .d {
            grid-column: 2;
            grid-row: 3;
        }
    }
}

//display grid en sobre-la-web.html

.about-wrapper {
    @include lg {
        display: grid;
        grid-gap: 10px;
        grid-template-columns: auto auto auto;
        section {
            border-radius: 5px;
            padding: 10px;
        }
        .a {
            grid-column: 1 / 3;
            grid-row: 1;
        }
        .b {
            grid-column: 3 ;
            grid-row: 1 / 3;
        }
        .c {
            grid-column: 1 / 3;
            grid-row: 2 ;
        }
    }
}

//display grid en pÃ¡ginas de detalle
.divider.enlaces {
    @include lg {
        display: none;
    }
}

//galletas
.galletas-wrapper {
    @include lg {
        display: grid;
        grid-gap: 10px;
        grid-template-columns: auto auto;
        div {
            border-radius: 5px;
            padding-left: 10px;
        }
        .a {
            grid-column: 1 / 2;
            grid-row: 1 / 2;
        }
        .b {
            grid-column: 3 ;
            grid-row: 1;
        }
        .c {
            grid-column: 1 / 2;
            grid-row: 2 ;
        }
        .d {
            grid-column: 3;
            grid-row: 2 / 3;
        }
    }
    @include xl {
        grid-template-columns: auto auto auto;
        .a {
            grid-column: 1 / 3;
            grid-row: 1;
        }
        .b {
            grid-column: 3 ;
            grid-row: 1;
        }
        .c {
            grid-column: 1 / 2;
            grid-row: 2 ;
        }
        .d {
            grid-column: 2 / 4;
            grid-row: 2;
        }
    }
}

//curiosidades
.curiosidades-wrapper {
    @include lg {
        display: grid;
        grid-gap: 10px;
        grid-template-columns: auto auto;
        section {
            padding-left: 10px;
        }
        .a {
            grid-column: 1;
            grid-row: 1;
        }
        .b {
            grid-column: 2;
            grid-row: 1 / 3;
        }
        .c {
            grid-column: 1;
            grid-row: 2 / 4;
        }
        .d {
            grid-column: 2;
            grid-row: 3 / 5;
        }
        .e {
            grid-column: 1 / 2;
            grid-row: 4 / 5;
        }
    }
    @include xl {
        grid-template-columns: auto auto;
        .a {
            grid-column: 1;
            grid-row: 1;
        }
        .b {
            grid-column: 2;
            grid-row: 1;
        }
        .c {
            grid-column: 1 / 3;
            grid-row: 2;
        }
        .d {
            grid-column: 1;
            grid-row: 3;
        }
        .e {
            grid-column: 2;
            grid-row: 3;
        }
    }
}

