//display grid en categorias.html

.categories-wrapper {
    @include lg {
        hr {
            display: none;
        }
        display: grid;
        grid-gap: 10px;
        grid-template-columns: auto auto auto auto;
        section {
            padding: 0 10px;
        }
        .submenu {
            grid-column: 1 / 2;
            grid-row: 1;
        }
        figure {
            grid-column: 2 / 4;
            grid-row: 1;
            padding: 40px 50px 0 50px;
        }
        #lugares {
            grid-column: 1 / 2;
            grid-row: 2;
            vertical-align: middle;
        }
        #escenas {
            grid-column: 1 / 2;
            grid-row: 3;
            vertical-align: middle;
        }
        #curiosidades {
            grid-column: 2 / 4;
            grid-row: 2;
            vertical-align: middle;
            padding-right: 50px;
        }
        #homages {
            grid-column: 2 / 4;
            grid-row: 3;
            vertical-align: middle;
            padding-right: 50px;
        }
    }
    @include xl {
        #lugares {
            grid-column: 2 / 3;
            grid-row: 2;
            vertical-align: middle;
        }
        #escenas {
            grid-column: 2 / 3;
            grid-row: 3;
            vertical-align: middle;
        }
        #curiosidades {
            grid-column: 3 / 4;
            grid-row: 2;
            vertical-align: middle;
        }
        #homages {
            grid-column: 3 / 4;
            grid-row: 3;
            vertical-align: middle;
        }
    }
}

